import React, { Component } from 'react'

import Modal from "./Modal"

export default class BandModal extends Component {
	render() {
		let { closeModal, band } = this.props
		return(
			<Modal
				customClass="BandModal"
				closeModal={closeModal}
			>
				<div className="band-name">
					{band.bandName}
				</div>
				<div className="desc">{band.bandDescription}</div>
			</Modal>
		)
	}
}
