											<p className="body">Au départ, il y a eu Adeline et Dorine, deux copines de longue date qui souhaitaient organiser une soirée de départ pour d’autres amis qui changeaient de vie. Plutôt que d’opter pour la traditionnelle salle de fêtes avec DJ et open bar, nos deux rêveuses ont imaginé une fête dans les champs. Mais l’imagination débordante des deux organisatrices en herbe a très vite transformé cela en un véritable événement à part entière.</p>
