import React, { Component } from 'react'
import { ParallaxLayer } from 'react-spring/renderprops-addons'

import PixiText from './Pixi'
import Arrow from './static/arrow-svg'

export default class SectionCard extends Component {
	state = {
		currentSlide: 0
	}
	nextSlide = () => {
		let { currentSlide } = this.state
		let { slides } = this.props
		this.setState({ currentSlide: slides[currentSlide + 1] ? (currentSlide + 1) : 0 })
	}
	prevSlide = () => {
		let { currentSlide } = this.state
		let { slides } = this.props
		this.setState({ currentSlide: slides[currentSlide - 1] ? (currentSlide - 1) : (slides.length - 1) })
	}
	render() {
		let { currentSlide } = this.state
		let {
			sectionTitle,
			carrot,
			offset,
			speed,
			slides = [],
		} = this.props

		return (
			<ParallaxLayer className="SectionCard" offset={offset} speed={speed} factor={0.5}>
				<PixiText customClass="section-title" text={sectionTitle}/>
				{slides.length > 1 && <Arrow onClick={this.prevSlide}/>}
				<div className="container" onClick={this.showPixi}>
					<div className="slider-container">
						<div
							className="slider"
							style={{
								width: `${currentSlide.length * 100}%`,
								transform: `translateX(-${currentSlide * 100 / slides.length}%)`
							}}
						>
							{slides.map((entry, index) =>
								<div key={index} className="slide">
									<div className="content">
										<PixiText text={entry.slideTitle}/>
										<div className={"slide-content " + entry.contentClass}>{entry.content}</div>
									</div>
								</div>
							)}
						</div>
					</div>
				</div>
				{slides.length > 1 && <Arrow right={true} onClick={this.nextSlide}/>}
				<img className="carrot" alt="band" src={carrot}/>
			</ParallaxLayer>
		)
	}
}


