import React, { Component } from 'react'
import * as PIXI from 'pixi.js'
import FontFaceObserver from 'fontfaceobserver'

import rippleSprite from "./static/ripple-sprite.jpg"

export default class PixiText extends Component {
	constructor(props) {
		super(props)
		this.pixiView = React.createRef()
	}
	componentWillMount = async () => {
		let font = new FontFaceObserver("titleFont")
		font.load().then(() => this.setup())
	}
	setup = () => {
		this.app = new PIXI.Application({
			width: 550,
			height: 100,
			transparent: true,
		})
		this.pixiView.current.appendChild(this.app.view)
		this.app.stage.interactive = true
		this.container = new PIXI.Container()

		let textSample = new PIXI.Text(
			this.props.text,
			{ fontFamily: "titleFont", fontSize: 70, fill: "white" }
		)
		this.app.stage.addChild(this.container)
		this.container.addChild(textSample)
		textSample.x = 10
		textSample.y = 10

		const displacementSprite = new PIXI.Sprite.from(rippleSprite)
		displacementSprite.texture.baseTexture.wrapMode = PIXI.WRAP_MODES.REPEAT
		const displacementFilter = new PIXI.filters.DisplacementFilter(displacementSprite)
		displacementFilter.padding = 10

		this.container.addChild(displacementSprite)
		textSample.filters = [displacementFilter]
		displacementFilter.scale.x = 10
		displacementFilter.scale.y = 10

		this.app.ticker.add(() => {
			displacementSprite.x += 5
			displacementSprite.y += 5
			if(displacementSprite.x > displacementSprite.width){
				displacementSprite.x = 0
			}
			if(displacementSprite.y > displacementSprite.height){
				displacementSprite.y = 0
			}
		})
	}
	render() {
		let { customClass = "" } = this.props
		return (
			<div
				className={"Pixi " + customClass}
				ref={this.pixiView}
			/>
		)
	}
}

