import React, { Component } from 'react'

import Arrow from './static/arrow-svg'
import { CrossButton } from './FnCrossButton'
import Modal from "./Modal"

const galleryFolderImages = require.context( "./static/gallery", false, /.*/ )

// galleryFolderImages(entry)

let imgNames = galleryFolderImages.keys().reduce((acc, entry) => {
	if(entry.indexOf("_tn") > -1){
		return acc
	}
	acc.push({ path: entry })
	return acc
}, [])

const IMAGES = imgNames.map(entry => {
	let idx = entry.path.lastIndexOf(".");
	let thumbPath = entry.path.substr(0, idx) + "_tn" + entry.path.substr(idx)
	return {
		src: galleryFolderImages(entry.path),
		thumbnail: galleryFolderImages(thumbPath),
		// thumbnailWidth: 256,
		// thumbnailHeight: 192,
	}
})

export default class Gallery extends Component {
	state = {
		showImgModal: false,
		currImgIndex: 0
	}
	componentDidMount = () => {
		this.setupImgLazyLoad()
	}
	setupImgLazyLoad = () => {
		const images = document.querySelectorAll('.lazy-load')
		if(!('IntersectionObserver' in window)){
			images.forEach(img => this.loadImg(img))
			return
		}
		let observer = new IntersectionObserver(this.handleImgEntersIntoView, {
			rootMargin: '50px 0px',
			threshold: 0.01
		})
		images.forEach(entry => observer.observe(entry))
		this.setState({ observer })
	}
	handleImgEntersIntoView = observedArr => {
		let { observer } = this.state
		if(observedArr.length === 0){
			observer.disconnect()
		}
		observedArr.forEach(entry => {
			if(entry.intersectionRatio > 0){
				observer.unobserve(entry.target)
				this.loadImg(entry.target)
			}
		})
	}
	loadImg = img => {
		let src = img.dataset.src
		img.src = src
	}
	showFullSizeImg = e => {
		let { index } = e.target.dataset
		this.setState({ showImgModal: true, currImgIndex: parseInt(index, 10) })
	}
	closeModal = () => {
		this.setState({ showImgModal: false })
	}
	nextPic = e => {
		e.stopPropagation()
		let nextSlide = (this.state.currImgIndex + 1) % IMAGES.length
		this.goToSlide(nextSlide)
	}
	prevPic = e => {
		e.stopPropagation()
		let nextSlide = (this.state.currImgIndex - 1 + IMAGES.length) % IMAGES.length
		this.goToSlide(nextSlide)
	}
	goToSlide = index => {
		this.setState({ currImgIndex: index })
	}
	stopPropagation = e => {
		e.stopPropagation()
	}
	render(){
		let { showImgModal, currImgIndex } = this.state
		let currImgSrc = IMAGES[currImgIndex].src
		return(
			<div className="Gallery">
				{showImgModal &&
					<Modal
						customClass="img-modal"
						closeModal={this.closeModal}
					>
						<Arrow onClick={this.prevPic}/>
						<div className="img-container" onClick={this.stopPropagation}>
							<CrossButton onClick={this.closeModal}/>
							<img className="curr-img" src={currImgSrc} alt="fest"/>
						</div>
						<Arrow right={true} onClick={this.nextPic}/>
					</Modal>
				}
				{IMAGES.filter((e, i) => i < 5).map((entry, index) =>
					<div key={entry.src} className="cell">
						<img
							className="lazy-load thumb"
							data-index={index}
							data-src={entry.thumbnail}
							onClick={this.showFullSizeImg}
							alt="thumbnail"
						/>
					</div>
				)}
			</div>
		)
	}
}
