import React, { Component } from 'react'
import ReactDOM from 'react-dom';

import { CrossButton } from './FnCrossButton'

export default class Modal extends Component {
	stopPropagation = e => {
		e.stopPropagation()
	}
	render() {
		let { closeModal, customClass = "", children } = this.props
		return(
			ReactDOM.createPortal(
				<div className="modal-wrapper" onClick={closeModal}>
					<div className={"content " + customClass} onClick={this.stopPropagation}>
						<CrossButton onClick={closeModal}/>
						{children}
					</div>
				</div>,
				document.getElementById('modal-anchor')
			)
		)
	}
}
